// File: app/(auth)/login/page.tsx
"use client";
import React, { useState } from "react";

async function postJSON2(url: string, body: any) {
  const res = await fetch(url, {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify(body),
  });
  return res.json();
}

export default function LoginPage() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  async function handleLogin(e?: React.FormEvent) {
    e?.preventDefault();
    setError(null);
    setLoading(true);
    try {
      const res = await postJSON2(`/api/auth/login`, { email, password });
      if (res?.ok && res?.sessionToken) {
        // store session token (in cookie ideally). For demo, store in localStorage then redirect
        localStorage.setItem("sessionToken", res.sessionToken);
        window.location.href = "/";
      } else setError(res?.error || "Invalid credentials");
    } catch (err: any) {
      setError(String(err));
    } finally {
      setLoading(false);
    }
  }

  function startGoogleOAuthLogin() {
    window.location.href = "/api/auth/oauth/google/start?flow=login";
  }

  return (
    <div className="max-w-md mx-auto p-6 bg-white rounded-2xl shadow-lg">
      <h2 className="text-2xl font-semibold mb-4">Sign in</h2>
      {error && <div className="text-sm text-red-600 mb-3">{error}</div>}

      <button onClick={startGoogleOAuthLogin} className="w-full p-3 border rounded-lg flex items-center justify-center gap-3 mb-4">
        <img src="/icons/google.svg" alt="Google" className="w-5 h-5" />
        Continue with Google
      </button>

      <div className="text-center text-sm text-slate-500 mb-4">or</div>

      <form onSubmit={handleLogin} className="space-y-3">
        <label className="block text-sm">Email</label>
        <input type="email" required value={email} onChange={(e) => setEmail(e.target.value)} className="w-full p-2 border rounded" />

        <label className="block text-sm">Password</label>
        <input type="password" required value={password} onChange={(e) => setPassword(e.target.value)} className="w-full p-2 border rounded" />

        <div className="flex gap-2">
          <button type="submit" disabled={loading} className="px-4 py-2 bg-slate-900 text-white rounded">Sign in</button>
          <a className="px-4 py-2 border rounded text-sm" href="/auth/forgot">Forgot?</a>
        </div>
      </form>
    </div>
  );
}
